<template>
    <div class="interface-modal">
        <a-modal :title="title" :visible="regVisible" :mask=false :maskClosable=false @cancel="handleCancel"  :width="600" class="modalCls">
            <a-form-model ref="interfaceForm" :model="interfaceForm" :rules="rulesForm">
                <a-form-model-item label="接口名称" prop="name">
                    <a-input v-model="interfaceForm.name" placeholder="请输入接口名称"></a-input>
                </a-form-model-item>
                <a-form-model-item label="请求方法" prop="method">
                    <a-input v-model="interfaceForm.method" placeholder="请输入请求方法"></a-input>
                </a-form-model-item>
                <a-form-model-item label="请求资源" prop="url">
                    <a-input v-model="interfaceForm.url" placeholder="请输入请求资源"></a-input>
                </a-form-model-item>
                <a-form-model-item label="参数" prop="params">
                    <a-input v-model="interfaceForm.params" placeholder="请输入参数"></a-input>
                </a-form-model-item>
                <a-form-model-item label="是否启用">
                    <a-switch v-model="interfaceForm.isEnabled"></a-switch>
                </a-form-model-item>
                <a-form-model-item label="是否删除">
                    <a-switch v-model="interfaceForm.isDeleted"></a-switch>
                </a-form-model-item>
            </a-form-model>
            <template slot="footer">
                <a-button type="link" ghost @click="handleCancel">取消</a-button>
                <a-button type="primary" @click="handleOk">提交</a-button>
            </template>

        </a-modal>

    </div>
</template>

<script>
    export default {
        name: "interface-modal",
        props:{
            title:{
                type: String,
                default: "添加/编辑字典",
            },
            regVisible:{
                type:Boolean,
                default: false,
                required: true,
            },
            // 0为添加，1为编辑
            regType:{
                type:Number,
                default:0,
                required: true,
            },
            formContent: {
                type:Object,
                default:function () {
                    return{
                        dictKey:'',
                        dictValue:'',
                        tag:'',
                    }
                }
            }
        },
        data(){
            return{
                interfaceForm:{
                    name:'',
                    method:'',
                    url:'',
                    params:'',
                    isEnabled:true,
                    isDeleted:true,
                },
                rulesForm:{
                    name:[{ required: true, message: "请输入接口名称",trigger:'blur'}],
                    method:[{required: true,message:'请输入请求方法',trigger: 'blur'}],
                    url:[{required: true,message:'请输入请求资源',trigger: 'blur'}],
                    params:[{required: true,message:'请输入参数',trigger: 'blur'}]
                },
            }

        },
        methods:{
            handleOk(){
                this.$refs.interfaceForm.validate(valid =>{
                    if (valid){
                        let form = Object.assign({},this.interfaceForm,{isEnabled:this.interfaceForm.isEnabled?1:0},{isDeleted:this.interfaceForm.isDeleted?1:0});
                        this.$emit('handleOk',{formData:form,regType:this.regType})
                    } else {
                        this.$message.warning('输入有误，请检查');
                        return false
                    }
                })
            },
            handleCancel(){
                this.$emit('handleCancel','点击取消')
            }


        },
        watch:{
            formContent:{
                handler:function(newVal){
                    Object.assign(this.interfaceForm,newVal);
                },
                deep:true
            }
        }
    }
</script>
<style scoped>

</style>